body {
  margin: 0;
  padding: 0 0 0 0;
  font-family: 'Major Mono Display', monospace !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.navbar {
    margin-bottom: 30px;
}

code {
    font-family: 'Major Mono Display', monospace;
}

.clickable {
    cursor: pointer;
}

.list-group-item {
  cursor: pointer;
  font-family: "Major Mono Display", monospace;
  transition: background-color 500ms linear, color 500ms linear,
    box-shadow 500ms linear;
  justify-content: flex-end;
  font-weight: 500 !important;
}

.list-group-item:hover {
  background-color: #9a0bff !important;
  color: white;
  box-shadow: 0 0.3rem 0.5rem 0 rgba(149, 112, 76, 0.4), -2rem 0 #9a0bff inset;
}

.list-group-item.active {
  background-color: #9a0bff !important;
  border-color: #9a0bff !important;
}

.page-item.active .page-link {
    background-color: #9A0BFF !important;
    border-color: #9A0BFF !important;
}
.popular-articles {
    box-sizing: content-box;
    display: flex;
    margin: 0 0 8rem 0;
    padding: 5rem 0;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
}

.mini-card:focus-within, .mini-card:hover {
    -webkit-transform: translate(0,-1rem);
    transform: translate(0,-1rem);
}

.popular-articles .mini-card {
    scroll-snap-align: start;
}

.header-card {
    background-image: linear-gradient(to top,rgba(131, 79, 168, 0.788),#9A0BFF);
    position: relative;
    border-radius: 16px;
    padding: 2rem;
    margin: 0 4rem 1rem 0;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
}

.header-card-title {
    color: white;
    font-size: 1.6rem;
    margin: 0 0 .5rem 0;
    font-weight: 700;
}

.header-card p {
    margin: 0;
    font-size: 1.4rem;
    color: white;
}

.mini-card {
    width: 300px;
    min-height: 350px;
    padding: 1.5rem;
    border-radius: 16px;
    background: #17141d;
    box-shadow: -1rem 0 3rem #000;
    display: flex;
    flex-direction: column;
    transition: .2s;
    margin: 0;
}

.mini-card-grid {
    display: flex;
    margin-right: 30px;
}

.module {
    clear: both;
    margin: 0 0 1rem 0;
    position: relative;
}

.mini-article-card-header {
    margin-bottom: auto;
}

.mini-article-subhead {
    font-size: 14px;
    margin: 0 1rem 1rem 0 !important;
    color: white;
}

time {
    color: #9A0BFF;
    box-sizing: border-box;
    margin-left: 10px;
}

.mini-card h2 {
    font-size: 20px;
    margin: .25rem 0 auto 0;
    font-weight: 700;
}

h2 a {
    color: inherit;
    border: 0;
    display: inline-block;
    color: #fff !important;
    text-decoration: none;
}

.mini-article-meta {
    font-size: 14px;
}

.mini-article-byline {
    margin: 3rem 0 0 0;
    display: grid;
    grid-template-columns: 75px 1fr;
    align-items: center;
}

.author-avatar {
    grid-area: avatar;
    align-self: start;
    position: relative;
}

.mini-article-meta .author-name {
    display: block;
    position: relative;
}

.author-name, .comments-link {
    font-weight: 500;
    color: #fff;
    text-decoration: none;
}

.mini-card:hover~.mini-card {
    -webkit-transform: translateX(130px);
    transform: translateX(130px);
}

.mini-card:not(:first-child) {
    margin-left: -130px;
}
.moviesCount {
    line-height: 2;
    width: 300px;
    margin: 20px auto 20px 0;
}

.moviesCount span {
    background-color: #9A0BFF;
    font-size: 15px !important;
    color: #fff;
    display: inline;
    padding: 0.45rem;
    border-radius: 5px;

    /* Needs prefixing */
    box-decoration-break: clone;
    -webkit-box-decoration-break: clone;
}

/* DEMO-SPECIFIC STYLES */
.typewriter h1 {
    color: black;
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: .15em solid orange; /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    margin: 0 auto; /* Gives that scrolling effect as the typing happens */
    letter-spacing: .15em; /* Adjust as needed */
    -webkit-animation:
      typing 3.5s steps(30, end),
      blink-caret .5s step-end infinite;
            animation:
      typing 3.5s steps(30, end),
      blink-caret .5s step-end infinite;
  }

  @media only screen and (max-width: 600px) {
    .typewriter h1 {
      display: none;
    }
  }

  /* The typing effect */
  @-webkit-keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  @keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }

  /* The typewriter cursor effect */
  @-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: transparent }
  }
  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: transparent }
  }
.addBtn {
    margin-top: 10px;
    font-size: 15px !important;
    color: black !important;
    background-color: #fff !important;
    border: 1px solid black !important;
}

.addBtn:hover {
    color: #fff !important;
    background-color: #9A0BFF !important;
    border: 1px solid white !important;
}

